import React, { useState, useEffect, useMemo } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import AddNewAppointment from "./AddNewAppointment";
import { useDispatch, useSelector } from "react-redux";
import {
  getAllSlots,
  remainingAptPres,
} from "../../../app/features/doctorSchedule/doctorSchedule.slice";
import moment from "moment";
import { useLocation } from "react-router-dom";
import { FaCircle } from "react-icons/fa";
import GenericDatePicker from "../../../components/GenericDatePicker";

const Slots = () => {
  const dispatch = useDispatch();
  const [singleSlotData, setSingleSlotData] = useState("");
  const [slotDate, setSlotDate] = useState(new Date());
  const location = useLocation();
  const appointmentId = location?.state?.appointmentId;
  const [isSlotSelected, setIsSlotSelected] = useState(false);
  const [show, setShow] = useState(false);

  const { allSlots } = useSelector((state) => state.doctorSchedule);
  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  function getDayName(date = new Date(slotDate), locale = "en-US") {
    return date?.toLocaleDateString(locale, { weekday: "long" });
  }
  const dayName = getDayName();

  const displayDate = useMemo(() => {
    return slotDate
      ? `${dayName} - ${moment(slotDate).format("DD-MM-YYYY")}`
      : "Select Date";
  }, [slotDate, dayName]);

  useEffect(() => {
    if (slotDate) {
      const finalData = {
        date: moment(slotDate).format("YYYY-MM-DD"),
        month: null,
      };
      dispatch(getAllSlots(finalData));
    }
  }, [dispatch, slotDate]);

  const handleSlotSelection = (data) => {
    setSingleSlotData(data);
    setIsSlotSelected(true);
  };

  const handleShow = () => {
    setShow(true);
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(remainingAptPres(finalData));
  };
  const handleClose = () => setShow(false);

  return (
    <>
      <div className="d-flex justify-content-between">
        <h5>Available Slots</h5>
        {/* <div className="patient_apt-scheduling d-flex align-items-center">
          
          <Form.Select
            aria-label="Select Doctor"
            value=""
            style={{ width: "14rem" }}
            className="me-2"
          >
            <option value="">Select Doctor</option>
            <option value="1">Ralph Henry</option>
            <option value="2">Dale Smith</option>
          </Form.Select>
          <GenericDatePicker
            selectedDateState={slotDate}
            onChange={(date) => setSlotDate(date)}
            useDrDateClass={false}
            dateFormat="dd/MM/yyyy"
            placeholderText="day/month/year"
            mindate={new Date()}
          />
        </div> */}
      </div>
      <Card className="slotsSection mt-4">
        <Card.Body>
          <div className="d-flex flex-wrap justify-content-between align-items-center mb-4">
          <GenericDatePicker
            selectedDateState={slotDate}
            onChange={(date) => setSlotDate(date)}
            useDrDateClass={false}
            dateFormat="dd/MM/yyyy"
            placeholderText="day/month/year"
            mindate={new Date()}
          />
            <h5 className="text-black fw-bold mb-3 mx-3 d__date-text text-center">
              {displayDate}
            </h5>
            <div className="d-flex justify-content-end radioGroup mb-3">
              <div className="d-flex align-items-center me-sm-4 me-2">
                <span className="bookedSlot rounded-circle me-2">
                  <FaCircle color="#ff6060" size="22" />
                </span>
                <span>Booked Slots</span>
              </div>
              <div className="d-flex align-items-center">
                <span className="availableSlot rounded-circle me-2">
                  <FaCircle color="#81d363" size="22" />
                </span>
                <span>Available Slots</span>
              </div>
            </div>
          </div>

          <div className="doctor_Details">
            <div className="slotContainer mt-4 pt-3">
              {allSlots !== null && allSlots?.length > 0 ? (
                allSlots?.map((slotItem) => {
                  return (
                    <Button
                      key={slotItem?.startTime}
                      className={`slot_btn${
                        slotItem?.isBooked ? " booked" : ""
                      }`}
                      onClick={() => handleSlotSelection(slotItem)}
                    >
                      {slotItem?.startTime} - {slotItem?.endTime}
                    </Button>
                  );
                })
              ) : (
                <p className="text-center">No Slot Available</p>
              )}
            </div>
          </div>
          <div className="float-right mt-4">
            <Button
              onClick={handleShow}
              className="btn-primary px-5 primary_bg"
              disabled={singleSlotData?.isBooked === true || !singleSlotData}
            >
              Continue
            </Button>
          </div>
        </Card.Body>
        <AddNewAppointment
          show={show}
          handleClose={handleClose}
          singleSlotData={singleSlotData}
          appointmentId={appointmentId}
        />
      </Card>
    </>
  );
};

export default Slots;
